<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["current_arena"]),
    // ...mapGetters(["youth_team", "adult_team", "female_team"]),
    children_team() {
      return this.teams.filter((x) => x.type == "Детскaя");
    },
    youth_team() {
      return this.teams.filter((x) => x.type == "Юношеская");
    },
    adult_team() {
      return this.teams.filter((x) => x.type === "Взрослая");
    },
    female_team() {
      return this.teams.filter((x) => x.type === "Женская");
    },
  },
  filters: {
    descriptionLength(value) {
      if (!value) return "";
      if (value.length < 30) {
        return value;
      }
      return value.slice(0, 30) + "...";
    },
  },
  created() {
    const arenaId = this.$route.params.id;
    this.arenaId = arenaId;
    this.$store.dispatch("getArenaTeams", arenaId).then((data) => {
      console.log("Teams", data);
      this.teams = data.map((x) => x.team);
    });
    this.teams = this.current_arena.teams || [];

    const arenaItem = this.current_arena;
    this.sidebar_items = [
      { text: "Информация", link: `/arena/${arenaId}/information` },
      {
        text: "Платные услуги",
        link: `/arena/${arenaId}/payment_portal`,
      },
      {
        text: "Расписание мероприятий",
        link: `/arena/${arenaId}/event_schedule`,
      },
      { text: "Список команд", link: `/arena/${arenaId}/list_teams` },
      {
        text: "Тренерский состав",
        link: `/arena/${arenaId}/training_staff`,
      },
      {
        text: "Состав руководства",
        link: `/arena/${arenaId}/leadership`,
      },
    ];

    this.contact_list = [
      { icon: "mdi-whatsapp", link: `${arenaItem.whatsApp}` },
      { icon: "mdi-instagram", link: `${arenaItem.instagram}` },
      { icon: "mdi-vk", link: `${arenaItem.vk}` },
      { icon: "mdi-web", link: `${arenaItem.website}` },
      { icon: "mdi-music-note-outline", link: `${arenaItem.tiktok}` },
      { icon: "mdi-twitter", link: `${arenaItem.twitter}` },
      { icon: "mdi-youtube", link: `${arenaItem.youtube}` },
      { icon: "mdi-facebook", link: `${arenaItem.facebook}` },
    ];
    this.breadcrumb_items = [
      {
        text: "Москва",
        disabled: false,
        href: "/",
      },
      {
        text: `${this.current_arena.title}`,
        disabled: true,
        href: "/arena/id",
      },
    ];
  },
  data() {
    return {
      teams: [],
      contact_list: null,
      name: "ArenaTeamList",
      sidebar_tab: 0,
      sidebar_items: null,
      arenaId: null,
      premises_tab: null,
      premises_nav: [
        "Все команды",
        "Детские команды",
        "Юношеские команды",
        "Взрослые команды",
        "Женские команда",
      ],

      team_items: ["/team_room_1", "/team_room_2", "/team_room_3"],
    };
  },
};
</script>

